@import "../../global.scss";

.fun {
  background-color: $backgroundColor;
  text-align: center;
  height: auto;

  .gallery {
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    display: block;

    -webkit-column-count: 3;
    -moz-column-count: 3;
    column-count: 3;
    -webkit-column-width: 33%;
    -moz-column-width: 33%;
    column-width: 33%;
    padding: 0px 12px;

    .pic {
      max-width: 100%;
      padding-bottom: 12px;
    }
  }
}

@media (max-width: 991px) {
  .fun {
    .gallery {
      -webkit-column-count: 2;
      -moz-column-count: 2;
      column-count: 2;
    }
  }
}

@media (max-width: 500px) {
  .fun {
    .gallery {
      -webkit-column-count: 1;
      -moz-column-count: 1;
      column-count: 1;
      -webkit-column-width: 100%;
      -moz-column-width: 100%;
      column-width: 100%;
    }
  }
}
